@import url(https://fonts.googleapis.com/css?family=Roboto:400,700);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html, body {
  overflow: hidden;
  height: 100%;
  width: 100%;
  overflow-y: auto;
}

body {
  font-family: "Roboto", "Arial", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #121212;
}

svg {
  display: block;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  width: 100%;
  height: 100%;
  position: relative;
}

text {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-family: 'Roboto', "Arial", "Helvetica Neue", sans-serif;
}

.menu {
  width: 100%;
  height: 100%;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-direction: column;
}

.menu::before, .menu::after {
  content: '';  /* Insert pseudo-element */
  margin: auto; /* Make it push flex items to the center */
}

.menu ul {
  color: white;
  list-style: none;
  padding: 8px;
}

.button {
  text-align: center;
  display: block;
  padding: 12px;
  margin: 8px;
  border-radius: 4px;
  font-size: 24px;
  font-weight: bold;
  background-color: #232323;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.menu li.button {
  margin: 16px;
}

.menu li.button:hover {
  background-color: #434343;
}

.menu li.button:active {
  background-color: #333333;
}

.color-config-items {
  margin-left: auto;
  margin-right: auto;
}

.color-config-item {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.color-config-item * {
  margin-left: 12px;
}

.color-config-item svg {
  margin-left: 0px;
  overflow: visible;
}

.color-config-item input[type="text"] {
  padding: 6px;
  border-radius: 4px;
  display: inline-block;
  width: 6em;
}

/* The default input is annoying to style because
 * it supports accessibility; I might want to revise
 * this decision in the future. */
.color-config-item input[type="color"] {
  width: 0;
  height: 0;
  opacity: 0;
  overflow: hidden;
}

.button.save {
  background-color: #afabec;
  color: #121212;
}

.button.cancel, .button.reset {
  color: white;
  background-color: #232323;
}

.grid-block {
  -webkit-transition: fill 0.5s;
  transition: fill 0.5s;
  fill: transparent;
}

.grid-block-flash {
  /* Snap immeidately to this state.
     But then transition out based on the
     transitions for original classes */
  -webkit-transition: none;
  transition: none;

  stroke-width: 1;
  stroke: #FFFFFF22;
  fill: #C4C4C422;
}

.piece-block {
  -webkit-transition: none;
  transition: none;
  stroke: #ffffff;
}

.cannot-place {
  fill-opacity: 0.5;
  stroke: #232323;
}

#config-text-holder {
  flex: 0.5 1;
  padding-left: 24px;
  padding-right: 24px;
}

#config-text {
  width: 100%;
  height: 100%;
  min-height: 100%;
  padding: 8px;
  border-radius: 8px;
  resize: vertical;
  border-width: 3px;
}

.invalid-input {
  border-color: #ff5858;
}

.end-screen {
  position: relative;
  width: 100%;
  height: 100%;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
  color: white;
  display:flex;
  flex-direction: column;
  justify-content: space-around;
}

.mode-name {
  color: #cfabec;
}

.end-screen h1 {
  font-size: 36px;
  text-align: center;
  width: 100%;
}

.statistics {
  font-size: 24px;
  width: 100%;
  padding: 8px 8px;
}

.statistics * {
  padding: 4px;
}

.name {
  font-weight: bold;
}

.grid-line {
  stroke: #4F4F4F;
  stroke-width: 1;
}


text.text-outline {
  stroke: #838383;
}

/* TODO: use some preprocessor that understands variables, e.g. sass or less
 * so we can share values with the keyframes
 */
.ready, .go {
  stroke-width: 6;
  stroke: transparent;
  fill: white;
  font-size: 50pt;
  font-weight: 500;
  letter-spacing: 3pt;
}

/* This allows react to generate the appropriate
   vendor prefixes for 'animation' */
.go { -webkit-animation: go 250ms; animation: go 250ms; }
.ready-0 { -webkit-animation: ready-0 250ms; animation: ready-0 250ms; }
.ready-1 { -webkit-animation: ready-1 250ms; animation: ready-1 250ms; }
.ready-2 { -webkit-animation: ready-2 250ms; animation: ready-2 250ms; }
.ready-3 { -webkit-animation: ready-3 250ms; animation: ready-3 250ms; }

@-webkit-keyframes go {
  0% { font-size: 0pt; }
  75% { font-size: 55pt; }
  100% { font-size: 50pt; }
}

@keyframes go {
  0% { font-size: 0pt; }
  75% { font-size: 55pt; }
  100% { font-size: 50pt; }
}

@-webkit-keyframes ready-0 {
  0% { -webkit-transform: translate(-300pt,0); transform: translate(-300pt,0); }
  75% { -webkit-transform: translate(10pt,0); transform: translate(10pt,0); }
  100% { -webkit-transform: translate(0,0); transform: translate(0,0); }
}

@keyframes ready-0 {
  0% { -webkit-transform: translate(-300pt,0); transform: translate(-300pt,0); }
  75% { -webkit-transform: translate(10pt,0); transform: translate(10pt,0); }
  100% { -webkit-transform: translate(0,0); transform: translate(0,0); }
}

@-webkit-keyframes ready-1 {
  0% { -webkit-transform: translate(300pt,0); transform: translate(300pt,0); }
  75% { -webkit-transform: translate(-10pt,0); transform: translate(-10pt,0); }
  100% { -webkit-transform: translate(0,0); transform: translate(0,0); }
}

@keyframes ready-1 {
  0% { -webkit-transform: translate(300pt,0); transform: translate(300pt,0); }
  75% { -webkit-transform: translate(-10pt,0); transform: translate(-10pt,0); }
  100% { -webkit-transform: translate(0,0); transform: translate(0,0); }
}

@-webkit-keyframes ready-2 {
  0% { -webkit-transform: translate(0, 300pt); transform: translate(0, 300pt); }
  75% { -webkit-transform: translate(0, -10pt); transform: translate(0, -10pt); }
  100% { -webkit-transform: translate(0,0); transform: translate(0,0); }
}

@keyframes ready-2 {
  0% { -webkit-transform: translate(0, 300pt); transform: translate(0, 300pt); }
  75% { -webkit-transform: translate(0, -10pt); transform: translate(0, -10pt); }
  100% { -webkit-transform: translate(0,0); transform: translate(0,0); }
}

@-webkit-keyframes ready-3 {
  0% { -webkit-transform: translate(0, -300pt); transform: translate(0, -300pt); }
  75% { -webkit-transform: translate(0, 10pt); transform: translate(0, 10pt); }
  100% { -webkit-transform: translate(0,0); transform: translate(0,0); }
}

@keyframes ready-3 {
  0% { -webkit-transform: translate(0, -300pt); transform: translate(0, -300pt); }
  75% { -webkit-transform: translate(0, 10pt); transform: translate(0, 10pt); }
  100% { -webkit-transform: translate(0,0); transform: translate(0,0); }
}



.pause {
  stroke-width: 6;
  stroke: transparent;
  fill: white;
  font-size: 40pt;
  font-weight: 500;
  letter-spacing: 3pt;
  -webkit-animation: pause 250ms;
          animation: pause 250ms;
}

@-webkit-keyframes pause {
  0% { font-size: 0pt; }
  75% { font-size: 45pt; }
  100% { font-size: 40pt; }
}

@keyframes pause {
  0% { font-size: 0pt; }
  75% { font-size: 45pt; }
  100% { font-size: 40pt; }
}

